import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import '../styles/global.css'; // Importando o CSS global

const UserManagement: React.FC = () => {
  const [users, setUsers] = useState<any[]>([]);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const data = await getUserList(); // Chamada ao serviço para obter a lista de usuários
        setUsers(data);
      } catch (err) {
        setError('Erro ao carregar a lista de usuários.');
      }
    };

    fetchUsers();
  }, []);

  return (
    <div style={{ display: 'flex' }}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px', width: '100%' }}>
        <h1 style={{ color: '#333', borderBottom: '2px solid #ddd', paddingBottom: '10px' }}>
          Gerenciamento de Usuários
        </h1>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <ul style={{ listStyle: 'none', padding: '0', margin: '20px 0' }}>
          {users.map((user: any) => (
            <li
              key={user.id}
              style={{
                padding: '10px 15px',
                margin: '10px 0',
                backgroundColor: '#f9f9f9',
                border: '1px solid #ddd',
                borderRadius: '5px',
              }}
            >
              <strong>{user.name}</strong> (<em>{user.email}</em>) - ID: {user.id}{' '}
              <div style={{ marginTop: '10px' }}>
                <button
                  style={{
                    marginRight: '10px',
                    padding: '5px 10px',
                    backgroundColor: '#4CAF50',
                    color: 'white',
                    border: 'none',
                    borderRadius: '5px',
                  }}
                  onClick={() => alert(`Editando usuário ${user.name}`)}
                >
                  Editar
                </button>
                <button
                  style={{
                    padding: '5px 10px',
                    backgroundColor: '#f44336',
                    color: 'white',
                    border: 'none',
                    borderRadius: '5px',
                  }}
                  onClick={() => alert(`Excluindo usuário ${user.name}`)}
                >
                  Excluir
                </button>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default UserManagement;
